import React from 'react';

const PlayerControls = ({ isPlaying, playSong, pauseSong, nextSong, prevSong }) => {
  return (
    <div className="flex items-center justify-center space-x-6">
      {/* Previous Button */}
      <button
        onClick={prevSong}
        className="p-3 bg-gray-700 rounded-full text-white hover:bg-gray-600 transition duration-300"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M15 19l-7-7 7-7"
          />
        </svg>
      </button>

      {/* Play/Pause Button */}
      <button
        onClick={isPlaying ? pauseSong : playSong}
        className="p-4 bg-blue-500 rounded-full text-white hover:bg-blue-400 transition duration-300"
      >
        {isPlaying ? (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-8 w-8"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d="M6 19V6l15 7-15 7z"
            />
          </svg>
        ) : (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-8 w-8"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d="M14 2H6a2 2 0 00-2 2v16a2 2 0 002 2h8a2 2 0 002-2V4a2 2 0 00-2-2z"
            />
          </svg>
        )}
      </button>

      {/* Next Button */}
      <button
        onClick={nextSong}
        className="p-3 bg-gray-700 rounded-full text-white hover:bg-gray-600 transition duration-300"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M9 5l7 7-7 7"
          />
        </svg>
      </button>
    </div>
  );
};

export default PlayerControls;
